'use client';
import React, { useState, useEffect } from 'react';
import { getAllMediasForPhotographer } from "../../app/lib/prisma-db.js";
import Image from "next/image";
import styles from '../../page.module.css';

export default function PhotographerGallery({ photographerId }) {
  const [medias, setMedias] = useState([]);
  const [sortType, setSortType] = useState('popularity');

  useEffect(() => {
    async function fetchMedias() {
      const result = await getAllMediasForPhotographer(photographerId);
      setMedias(result);
    }
    fetchMedias();
  }, [photographerId]);

  // Tri en fonction de sortType
  const sortedMedias = [...medias].sort((a, b) => {
    if (sortType === 'popularity') return b.likes - a.likes;
    if (sortType === 'date') return new Date(b.date) - new Date(a.date);
    if (sortType === 'title') return a.title.localeCompare(b.title);
    return 0;
  });

  return (
    <div>
      <div className={styles.sortContainer}>
        <label>Trier par</label>
        <select value={sortType} onChange={(e) => setSortType(e.target.value)}>
          <option value="popularity">Popularit√©</option>
          <option value="date">Date</option>
          <option value="title">Titre</option>
        </select>
      </div>

      <div className={styles.gallery}>
        {sortedMedias.map(media => (
          <div key={media.id} className={styles.mediaCard}>
            {media.image && (
              <Image
                src={`/assets/${media.image}`}
                alt={media.title}
                width={350}
                height={300}
                style={{ objectFit: 'cover', borderRadius: '5px' }}
              />
            )}
            {media.video && (
              <video width={350} height={300} controls>
                <source src={`/assets/${media.video}`} type="video/mp4" />
              </video>
            )}
            <p>{media.title}</p>
          </div>
        ))}
      </div>
    </div>
  );
}
